@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
}
:root {
  min-height: 100%;
  --box-shadow: 7px 7px 19px 0px rgba(50, 50, 50, 0.28);
}
body {
  min-height: 100%;
  margin: 0px;
  padding: 0px;
  font-family: "Lato", sans-serif;
  background-color: #eee;
}
img {
  max-width: 100%;
}
.Header {
  background-color: #000;
  padding: 5px;
  display: flex;
  justify-content: space-around;
  color: #fff;
  align-items: center;
}
.Header h2 {
  font-weight: normal;
  margin: 0px;
}
.Header a {
  color: inherit;
}
.Cats {
  text-align: center;
  margin: 0 auto;
  padding: 20px;
}
.catslist {
  max-width: 960px;
  margin: 0 auto;
  padding: 10px;
  text-align: center;
  font-size: 1.2rem;
}

.catsgallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-auto-rows: 200px;
  grid-gap: 20px;
}
.catsgallery img {
  box-shadow: 7px 7px 19px 0px rgba(50, 50, 50, 0.28);
  box-shadow: var(--box-shadow);
}
button,
input,
select {
  display: inline-block;
  padding: 5px;
  font: inherit;
}

.loading {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  text-align: center;
  -webkit-animation-name: spinner;
          animation-name: spinner;
  -webkit-animation-duration: 0.7s;
          animation-duration: 0.7s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  border: 5px solid transparent;
  border-top-color: #000;
  border-bottom-color: #000;
  margin: 0 auto;
  margin-top: 100px;
}

@-webkit-keyframes spinner {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spinner {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Cats {
  display: flex;
  flex-direction: column;
}

.Cat img {
  border-radius: 50%;
}

.Banner {
  max-width: 960px;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  margin: 0 auto;
  grid-auto-rows: auto;
}
.navigation {
  display: flex;
  justify-content: center;
}

/* lord of the rings chale. */
.Lotr {
  max-width: 960px;
  margin: 10px auto;
}
.Lotr .box {
  display: flex;
  flex-wrap: wrap;
}
.Lotr .box .Character {
  flex-grow: 1;
  flex-basis: 200px;
  flex-shrink: 0;
  padding: 10px;
  margin: 10px;
  text-align: center;
  border-radius: 5px;
  box-shadow: 7px 7px 19px 0px rgba(50, 50, 50, 0.28);
  box-shadow: var(--box-shadow);
}
.Home {
  background-image: radial-gradient(
    circle at top left,
    rgba(177, 235, 232, 0.404),
    rgba(198, 198, 235, 0.301)
  );
  height: calc(100vh - 39px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

